.header {
  position: fixed;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 500;
  transition: background-color 0.3s ease 0s;
}
.header.active {
  background-color: rgba(255, 255, 255, 0.8);
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
}
@media (any-hover: hover) {
  .header.active:hover {
    background-color: #ffffff;
    transition: background-color 0.3s ease 0s;
  }
}
.header__container {
  height: 100%;
  width: 100%;
}
.header__body {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 70px;
}
.header__logo {
  font-size: 25px;
  color: #10221b;
  font-weight: 900;
  flex: 0 0 180px;
  margin-right: 20px;
}
.header__logo:before {
  display: inline;
  color: #219150;
  padding-right: 5px;
}
.header__main {
  display: flex;
  align-items: center;
}
@media (max-width: 992px) {
  .header__navbar {
    position: fixed;
    top: 0;
    left: 0;
    transform: translateX(-100%);
    width: 60%;
    height: 100vh;
    z-index: 1000;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow-y: auto;
    overflow-x: hidden;
    background-color: #ffffff;
    padding: 40px 20px;
    opacity: 0;
    visibility: hidden;
    transition: transform 0.5s ease, box-shadow 0.8s ease, opacity 0.5s ease, visibility 0.5s ease;
  }
  .header__navbar.active {
    opacity: 1;
    visibility: visible;
    transform: translateX(-10%);
    box-shadow: 0 0 0 100vw rgba(0, 0, 0, 0.8);
    transition: transform 0.5s ease, opacity 0.5s ease, visibility 0.5s ease, box-shadow 1.2s ease;
  }
}
@media (max-width: 768px) {
  .header__navbar {
    width: 60%;
  }
}
@media (max-width: 480px) {
  .header__navbar {
    width: 85%;
    flex-direction: column;
  }
}
.header__menu {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
@media (max-width: 992px) {
  .header__menu {
    flex-wrap: nowrap;
    flex-direction: column;
  }
}
@media (max-width: 480px) {
  .header__menu {
    margin-bottom: 40px;
  }
}
.header__item {
  margin: 10px 0;
}
.header__item:not(:last-child) {
  margin-right: 25px;
}
@media (max-width: 992px) {
  .header__item {
    margin: 0;
  }
  .header__item:not(:last-child) {
    margin: 0 0 40px 0;
  }
}
@media (max-width: 480px) {
  .header__item {
    margin: 0;
  }
  .header__item:not(:last-child) {
    margin: 0 0 25px 0;
  }
}
.header__link {
  font-size: 18px;
  font-weight: 700;
  transition: color 0.3s ease 0s;
}
@media (any-hover: hover) {
  .header__link:hover {
    color: #219150;
  }
}
@media (max-width: 992px) {
  .header__link {
    font-size: 42px;
  }
}
@media (max-width: 768px) {
  .header__link {
    font-size: 36px;
  }
}
@media (max-width: 480px) {
  .header__link {
    font-size: 26px;
  }
}
.header__actions {
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
}
.header__user, .header__search,
.header .cart-header__btn {
  transition: color 0.3s ease 0s;
}
@media (any-hover: hover) {
  .header__user:hover, .header__search:hover,
.header .cart-header__btn:hover {
    color: #219150;
  }
  .header__user:hover span, .header__search:hover span,
.header .cart-header__btn:hover span {
    background-color: #146436;
  }
}
.header__user {
  font-size: 20px;
  margin-right: 20px;
}
@media (max-width: 480px) {
  .header__user {
    margin-right: 40px;
  }
}
.header__cart {
  position: relative;
  margin-right: 20px;
}
@media (any-hover: hover) {
  .header__cart.active:hover .cart-header__content {
    opacity: 1;
    visibility: visible;
    transition: opacity 0.3s ease, visibility 0.3s ease;
  }
}
@media (max-width: 480px) {
  .header__cart {
    margin-right: 25px;
  }
}
.header__search {
  font-size: 20px;
}
@media (max-width: 992px) {
  .header__search {
    margin-right: 20px;
  }
}
@media (max-width: 480px) {
  .header__search {
    margin-right: 0;
  }
}
.header__icons {
  display: none;
}
@media (max-width: 480px) {
  .header__icons {
    display: flex;
    align-items: center;
  }
}
.header__burger {
  display: none;
}
@media (max-width: 992px) {
  .header__burger {
    display: block;
    position: relative;
    width: 30px;
    height: 16px;
    cursor: pointer;
    z-index: 1001;
    transition: all 0.3s ease 0s;
  }
}
@media (max-width: 992px) and (any-hover: hover) {
  .header__burger:hover span {
    background-color: #219150;
  }
}
@media (max-width: 992px) {
  .header__burger span {
    position: absolute;
    height: 2px;
    width: 100%;
    background-color: #10221b;
    transition: all 0.3s ease 0s;
    left: 0;
  }
  .header__burger span:nth-child(1) {
    top: 0;
  }
  .header__burger span:nth-child(2) {
    top: 7px;
  }
  .header__burger span:nth-child(3) {
    bottom: 0;
  }
}
.header__burger.active span {
  transition: all 0.3s ease 0s;
  background-color: #ffffff;
}
.header__burger.active span:nth-child(1) {
  top: 7px;
  transform: rotate(-45deg);
}
.header__burger.active span:nth-child(2) {
  transform: scale(0);
}
.header__burger.active span:nth-child(3) {
  bottom: 7px;
  transform: rotate(45deg);
}
@media (any-hover: hover) {
  .header__burger.active:hover span {
    transition: all 0.3s ease 0s;
    background-color: #219150;
  }
}

.cart-header__btn {
  position: relative;
  font-size: 23px;
}
.cart-header__btn span {
  display: none;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: -12px;
  right: -12px;
  width: 22px;
  height: 22px;
  border-radius: 100%;
  background-color: #219150;
  color: #ffffff;
  font-size: 14px;
  font-weight: 600;
  transition: background-color 0.3s ease 0s;
}
.cart-header__btn span.active {
  display: inline-flex;
}
.cart-header__content {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  right: -40px;
  top: 220%;
  width: 345px;
  max-height: 250px;
  padding: 10px;
  background-color: #ffffff;
  transition: opacity 0.3s ease, visibility 0.3s ease;
}

.cart-content {
  box-shadow: 0 0 10px 10px rgba(16, 34, 27, 0.6);
  border-radius: 20px;
}
.cart-content .simplebar-offset {
  padding: 10px 0;
}
.cart-content__wrapper {
  overflow: auto;
  max-height: 180px;
}
.cart-content__item {
  display: flex;
  align-items: center;
  padding-right: 20px;
  transition: background-color 0.3s ease 0s;
  background-color: #ffffff;
}
.cart-content__item:not(:last-child) {
