  }
}
.cart-content__image {
  flex: 0 1 80px;
  height: 80px;
  margin-right: 10px;
}
.cart-content__image img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.cart-content__info {
  flex: 0 1 160px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 10px;
}
.cart-content__title {
  text-transform: uppercase;
  color: #219150;
  margin-bottom: 5px;
}
.cart-content__title span {
  display: block;
  font-size: 12px;
  font-weight: 900;
}
.cart-content__price {
  font-size: 14px;
  font-weight: 900;
  text-transform: uppercase;
  display: flex;
  align-items: flex-end;
  flex-direction: row-reverse;
  color: #10221b;
}
.cart-content__quantity {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 45px;
  width: 20px;
  height: 20px;
  border: 1px solid #10221b;
  background-color: #ffffff;
  color: #10221b;
  font-size: 12px;
  font-weight: 600;
}
.cart-content__remove {
  font-size: 18px;
  transition: transform 0.3s ease 0s;
  color: #10221b;
}
@media (any-hover: hover) {
  .cart-content__remove:hover {
    transform: scale(1.2);
  }
}
.cart-content__footer {
  padding: 10px;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
}
.cart-content__total {
  color: #219150;
  font-weight: 600;
  font-size: 20px;
}
.cart-content__total span {
  padding-left: 5px;
  color: #10221b;
  font-weight: 700;
  font-size: 18px;
}
.cart-content__btn {
  height: 40px;
  padding: 0 20px;
}

.simplebar-track.simplebar-vertical {
  width: 10px;
}

.header-item-active {
  animation-name: header-item;
  animation-delay: 0s;
}

@keyframes header-item {
  0% {
    transform: translateY(300%);
    opacity: 0;
    visibility: hidden;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
    visibility: visible;
  }
}
