  line-height: 140%;
  margin-bottom: 10px;
  transition: all 0.3s ease 0s;
}
.item-packages__price {
  font-size: 20px;
  color: #219150;
  margin-bottom: 10px;
  transition: all 0.3s ease 0s;
}
.item-packages__btn {
  flex: 0 0 45px;
  width: 190px;
  padding: 0 30px;
}
.item-packages__hidden {
  display: none;
}

.reviews__title {
  margin-bottom: 40px;
  text-align: center;
}
@media (max-width: 480px) {
  .reviews__title {
    margin-bottom: 25px;
  }
}
.reviews__item {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  min-height: 285px;
}
@media (max-width: 1200px) {
  .reviews__item {
    min-height: 308px;
  }
}
@media (max-width: 992px) {
  .reviews__item {
    min-height: 280px;
  }
}
@media (max-width: 730px) {
  .reviews__item {
    min-height: 380px;
  }
}
@media (max-width: 520px) {
  .reviews__item {
    min-height: 280px;
  }
}

.item-reviews__text {
  flex: 1 1 auto;
  padding: 20px;
  font-style: italic;
  background: #eee;
  border-radius: 5px;
  line-height: 150%;
  position: relative;
  z-index: 0;
  margin-bottom: 30px;
  margin-top: auto;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
}
.item-reviews__text:before {
  content: "";
  position: absolute;
  bottom: -13px;
  left: 7px;
  height: 30px;
  width: 30px;
  background-color: #eee;
  transform: rotate(45deg);
}
.item-reviews__user {
  display: flex;
  align-items: center;
}
.item-reviews__image {
  height: 70px;
  flex: 0 0 70px;
  margin-right: 10px;
}
.item-reviews__image img {
  border-radius: 100%;
}
.item-reviews__name {
  font-size: 20px;
  font-weight: 500;
}
.item-reviews__date {
  color: #219150;
}

.subscribe {
  background: #eee;
}
.subscribe__container {
  max-width: 700px;
  margin: 0 auto;
  padding: 0 15px;
}
@media (max-width: 480px) {
  .subscribe__container {
    padding: 0 10px;
  }
}
.subscribe__title {
  text-align: center;
  margin-bottom: 40px;
}
@media (max-width: 480px) {
  .subscribe__title {
    margin-bottom: 25px;
  }
}
.subscribe__text {
  line-height: 140%;
  margin-bottom: 20px;
}
.subscribe__form {
  position: relative;
  min-height: 50px;
  background-color: #ffffff;
  border-radius: 20px;
  border: 2px solid #10221b;
  padding: 5px;
}
.subscribe__form:focus-within {
  border-color: #219150;
}
.subscribe__label {
  display: block;
  width: 100%;
  height: 100%;
  padding-right: 140px;
}
.subscribe__label .js-validate-error-label {
  padding-left: 15px;
}
.subscribe__input {
  width: 100%;
  height: 100%;
  padding: 5px 15px;
  outline: none;
  border-radius: 20px;
}
.subscribe__btn {
  position: absolute;
  top: 5px;
  right: 5px;
  height: 35px;
  padding: 0 20px;
  border-radius: 20px;
  z-index: 5;
}

.blog__title {
  margin-bottom: 15px;
  text-align: center;
}
@media (max-width: 480px) {
  .blog__title {
    margin-bottom: 25px;
  }
}
.blog__slider {
  padding: 25px 0;
  padding: 30px;
}
@media (max-width: 480px) {
  .blog__slider {
    padding: 0;
  }
}
.blog__item {
  text-align: center;
  padding: 15px;
  border-radius: 20px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}
@media (any-hover: hover) {
  .blog__item:hover {
    transform: scale(1.05);
    box-shadow: 0 0 10px 10px rgba(0, 0, 0, 0.2);
  }
}
@media (max-width: 480px) {
  .blog__item {
    padding: 0;
  }
}

.item-blog__image {
  display: block;
  position: relative;
  height: 250px;
  width: 100%;
  margin-bottom: 30px;
  border-radius: 20px;
}
.item-blog__image img {
  border-radius: 20px;
}
@media (max-width: 480px) {
  .item-blog__image {
    margin-bottom: 20px;
  }
}
.item-blog__icons {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 50%);
  background-color: #10221b;
  padding: 10px 5px;
  min-width: 250px;
}
@media (max-width: 650px) {
  .item-blog__icons {
    min-width: 230px;
  }
}
.item-blog__icon {
  display: flex;
  align-items: center;
  font-size: 14px;
  color: #ffffff;
  padding: 0 5px;
}
.item-blog__icon:before {
  padding-right: 5px;
  color: #219150;
}
.item-blog__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
}
.item-blog__title {
  font-size: 20px;
  margin-bottom: 15px;
}
.item-blog__btn {
  flex: 0 0 45px;
  padding: 0 25px;
}

.footer {
  padding: 50px 0;
  overflow: hidden;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("../img/footer/footer.jpg") center/cover fixed no-repeat;
}
@media (max-width: 480px) {
  .footer {
    padding: 25px 10px;
  }
}
.footer__content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  justify-content: center;
  margin-bottom: 40px;
}
@media (max-width: 480px) {
  .footer__content {
    margin-bottom: 30px;
  }
}
.footer__column {
  margin-bottom: 15px;
}
.footer__credit {
  text-align: center;
  line-height: 140%;
  font-size: 20px;
  color: #ffffff;
}
.footer__credit a {
  color: #219150;
  transition: color 0.3s ease;
}
@media (any-hover: hover) {
  .footer__credit a:hover {
    color: #146436;
  }
}

.column-footer__title {
  font-size: 22px;
  margin-bottom: 25px;
  color: #ffffff;
}
@media (max-width: 480px) {
  .column-footer__title {
    margin-bottom: 20px;
  }
}
.column-footer__item {
  margin-bottom: 20px;
}
.column-footer__item:last-child {
  margin-bottom: 0;
}
.column-footer__link {
  display: inline-flex;
  align-items: center;
  font-size: 14px;
  color: #ffffff;
  transition: color 0.3s ease 0s;
}
.column-footer__link:before {
  display: inline-block;
  color: #219150;
  padding-right: 5px;
  transition: padding-right 0.3s ease 0s;
}
@media (any-hover: hover) {
  .column-footer__link:hover {
    color: #219150;
  }
  .column-footer__link:hover:before {
    padding-right: 15px;
  }
}